import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        menu: [],
        isLoading:false,
        refCount: 0,
        sportsmen:[],
        price:[],
        balance:{},
        transactions:[],
        nav_title:'',
        qay_mab:0,
        xis_top:0,
        kassa:0,
        aj_mab:0
    },
    mutations: {
        loading(state, isLoading) {
            console.log({isLoading})
            if (isLoading) {
                state.refCount++;
                state.isLoading = true;
            } else if (state.refCount > 0) {
                state.refCount--;
                state.isLoading = (state.refCount > 0);
            }
        },
        SET_MENU(state, users) {
            state.users = users;
        },
    },
    actions: {
    },
    modules: {
    }
})
